<template>
	<view class="container">
		<view class="main">
			<u--form :model="registerForm" :rules="rules" ref="form">
				<u-form-item prop="username" borderBottom>
					<u--input v-model="registerForm.username" clearable border="none" placeholder="请输入用户名"
						prefixIcon="account-fill"
						prefixIconStyle="font-size: 44rpx; color: #3c9cff; margin-right: 10rpx"></u--input>
				</u-form-item>
				<u-form-item prop="phonenumber" borderBottom>
					<u--input v-model="registerForm.phonenumber" clearable border="none" placeholder="请输入手机号码"
						prefixIcon="phone-fill"
						prefixIconStyle="font-size: 44rpx; color: #3c9cff; margin-right: 10rpx"></u--input>
				</u-form-item>
				<u-form-item prop="password" borderBottom>
					<u--input v-model="registerForm.password" type="password" clearable border="none"
						placeholder="请输入密码" prefixIcon="lock-fill"
						prefixIconStyle="font-size: 44rpx; color: #3c9cff; margin-right: 10rpx"></u--input>
				</u-form-item>
				<u-form-item prop="confirmPassword" borderBottom>
					<u--input v-model="registerForm.confirmPassword" type="password" clearable border="none"
						placeholder="请再次输入密码" prefixIcon="lock-fill"
						prefixIconStyle="font-size: 44rpx; color: #3c9cff; margin-right: 10rpx"></u--input>
				</u-form-item>
				<u-form-item prop="code" borderBottom>
					<!-- 注意：由于兼容性差异，如果需要使用前后插槽，nvue下需使用u--input，非nvue下需使用u-input -->
					<u-input placeholder="请输入验证码" v-model="registerForm.code" border="none" prefixIcon="integral-fill"
						prefixIconStyle="font-size: 44rpx; color: #3c9cff; margin-right: 10rpx">
						<template slot="suffix">
							<u--image :src="codeUrl" width="120px" height="30px" @click="getCode"></u--image>
						</template>
					</u-input>
				</u-form-item>
				<view style="margin-top:20px;"><u-button type="primary" text="绑 定 注 册"
						@click="handleRegister()"></u-button>
				</view>
			</u--form>

			<view class="copyright">
				<u--text size="8" type="info" align="center"
					text="Copyright © 2022-2023 wumei smart All Rights Reserved."></u--text>
			</view>
		</view>
	</view>
</template>

<script>
	import { captchaImage, bindRegister, getProfile } from '@/apis/modules/common.js';

	export default {
		data () {
			return {
				codeUrl: '',
				registerForm: {
					username: '',
					phonenumber: '',
					password: '',
					confirmPassword: '',
					code: '',
					uuid: '',
					bindId: ''
				},
				rules: {
					username: {
						type: 'string',
						min: 4,
						max: 20,
						required: true,
						message: '请输入用户名,长度大于4',
						trigger: ['blur', 'change']
					},
					phonenumber: [{
							type: 'integer',
							min: 11,
							max: 11,
							required: true,
							message: '请输入手机号码',
							trigger: ['blur', 'change']
						},
						{
							validator: (rule, value, callback) => {
								return uni.$u.test.mobile(value);
							},
							message: '手机号码格式不正确',
							// 触发器可以同时用blur和change
							trigger: ['change', 'blur']
						}
					],
					password: {
						type: 'string',
						required: true,
						min: 5,
						max: 20,
						message: '请输入密码且长度大于5',
						trigger: ['blur', 'change']
					},
					confirmPassword: [{
							type: 'string',
							required: true,
							message: '请输入密码',
							trigger: ['blur', 'change']
						},
						{
							validator: (rule, value, callback) => {
								if (this.registerForm.password !== value) {
									// uni.$u.test.mobile()就是返回true或者false的
									return false;
								} else {
									return true;
								}
							},
							message: '两次输入的密码不一致',
							// 触发器可以同时用blur和change
							trigger: ['change', 'blur']
						}
					],
					code: {
						type: 'integer',
						required: true,
						message: '请输入验证码',
						trigger: ['blur', 'change']
					}
				}
			};
		},
		onLoad (option) {
			//在加载时获取跳转后的数据
			this.registerForm.bindId = option.bindId;
			this.getCode();
		},
		methods: {
			// 获取验证码
			getCode () {
				captchaImage().then(res => {
					this.codeUrl = 'data:image/gif;base64,' + res.img;
					this.registerForm.uuid = res.uuid;
					this.registerForm.code = "";
				})
			},
			// 用户注册
			handleRegister () {
				this.$refs.form.validate().then(res => {
					// 调用注册绑定接口
					bindRegister(this.registerForm).then(async res => {
						if (res.code === 200) {
							// 存储token和账号
							this.saveToken(res.token);
							this.saveAccount();
							// 获取用户信息
							let profile = await this.getProfile();
							uni.$u.vuex('profile', profile);
							// 跳转主页
							uni.switchTab({
								url: '/pages/tabBar/home/index'
							});
						} else {
							if (res.msg) {
								uni.showToast({
									icon: "none",
									title: res.msg,
									complete: (res) => {
										setTimeout(() => {
											this.getCode();
										}, 1500);
									}
								})
							}
						}
					})
				})
			},
			// 获取用户信息
			getProfile () {
				return new Promise((resolve, reject) => {
					getProfile().then(res => {
						resolve(res.data);
					}).catch(err => {
						this.$u.toast(err.msg);
					})
				});
			},
			saveToken (token) {
				uni.setStorageSync('token', token); // 本地缓存存储token
				uni.$u.vuex('vuex_token', token); // vuex存储token
			},
			saveAccount () {
				// 本地缓存存储
				uni.setStorageSync('username', this.registerForm.username);
				uni.setStorageSync('password', this.registerForm.password);
			},
		}
	};
</script>
<style>
	.main {
		padding: 20px;
		background: #ffffff;
		border-top-left-radius: 0px;
		border-top-right-radius: 0px;
	}

	.copyright {
		position: fixed;
		bottom: 20px;
		left: 0;
		right: 0;
	}
</style>